.projects {
  background: linear-gradient(135deg, #4facfe, #00f2fe);
  padding: 2rem;
  border-radius: 15px;
  text-align: center;
  max-width: 800px;
  margin: 2rem auto;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-family: "Arial", sans-serif;
}

.projects ul {
  list-style: none;
  padding: 0;
}

.projects li {
  font-size: 1.2rem;
  margin: 0.5rem 0;
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.projects li:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.05);
  color: #ffcc00;
}

/* Media Query for Tablets */
@media (max-width: 768px) {
  .projects {
    padding: 1.5rem;
    max-width: 90%;
  }

  .projects ul {
    font-size: 1rem;
  }
}

/* Media Query for Phones */
@media (max-width: 480px) {
  .projects {
    padding: 1.2rem;
    max-width: 95%;
  }

  .projects ul {
    font-size: 1rem;
  }

  .projects li {
    padding: 0.5rem;
    font-size: 1rem;
    margin: 0.5rem 0;
  }
}
