body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(to right, #3c8ce7, #00eaff);
  color: white;
}

.header {
  background: #333;
  padding: 10px;
  text-align: center;
}

.header h1 {
  margin: 0;
  font-size: 1.5rem;
}

.header nav {
  margin-top: 10px;
}

.header nav a {
  margin: 0 10px;
  color: white;
  text-decoration: none;
}

.header nav a:hover {
  text-decoration: underline;
}

.home {
  text-align: center;
  padding: 20px;
}

.about,
.projects,
.contact {
  padding: 20px;
  text-align: center;
}

footer {
  background: #222;
  padding: 10px;
  text-align: center;
}

/* General Styling */
.projects {
  background: linear-gradient(135deg, #4facfe, #00f2fe);
  padding: 2rem;
  border-radius: 15px;
  text-align: center;
  max-width: 800px;
  margin: 2rem auto;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-family: "Arial", sans-serif;
}

.about-me {
  background: linear-gradient(135deg, #9fb8ad, #1fc8db); /* Colder gradient */
  padding: 2rem;
  border-radius: 15px;
  text-align: center;
  max-width: 800px;
  margin: 2rem auto;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-family: "Arial", sans-serif;
}

.about-me:hover {
  background: linear-gradient(135deg, #1fc8db, #9fb8ad); /* Reverse gradient */
  transform: scale(1.02); /* Slight zoom on hover */
  transition: transform 0.3s ease, background-color 0.5s ease;
}

.about-me p.personal-details {
  margin-top: 1rem;
  font-size: 1.2rem;
  line-height: 1.8;
  color: #ffffff;
}

.about-me p.details {
  margin-top: 1rem;
  font-size: 1.1rem;
  color: #ffeedd;
}

.projects ul {
  list-style: none;
  padding: 0;
}

.projects li {
  font-size: 1.2rem;
  margin: 0.5rem 0;
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.projects li:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.05);
  color: #ffcc00;
}

/* Media Query for Tablets */
@media (max-width: 768px) {
  .projects {
    padding: 1.5rem;
    max-width: 90%;
  }

  .projects h2 {
    font-size: 1.8rem;
  }

  .projects ul {
    font-size: 1rem;
  }
}

/* Media Query for Phones */
@media (max-width: 480px) {
  .projects {
    padding: 1.2rem;
    max-width: 95%;
  }

  .projects h2 {
    font-size: 1.6rem;
    margin-bottom: 1rem; /* Add space to balance layout */
  }

  .projects ul {
    font-size: 1rem;
  }

  .projects li {
    padding: 0.5rem;
    font-size: 1rem;
    margin: 0.5rem 0; /* Ensure enough space between items */
  }
}

/* Adjust profile image responsiveness */
.profile-image {
  width: 450px; /* Increased size for better visibility */
  height: 450px;
  border-radius: 50%;
  border: 3px solid #0078ff;
  margin: 1rem auto;
  display: block;
  object-fit: cover;
}

@media (max-width: 768px) {
  .profile-image {
    width: 350px;
    height: 350px;
    object-position: 15% center; /* Shift visible area slightly for balance */
  }
}

@media (max-width: 480px) {
  .profile-image {
    width: 300px;
    height: 300px;
    object-position: 10% center; /* Further shift visible area */
  }
}
