.about-me {
  background: linear-gradient(135deg, #3d05c0, #729ac5);
  padding: 2rem;
  border-radius: 15px;
  text-align: center;
  max-width: 800px;
  margin: 2rem auto;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-family: "Arial", sans-serif;
}

.about-me:hover {
  background: linear-gradient(135deg, #729ac5, #3d05c0); /* Reverse gradient */
  transform: scale(1.02); /* Slight zoom on hover */
  transition: transform 1s ease, background-color 1.5s ease;
}

.about-me p.personal-details {
  margin-top: 1rem;
  font-size: 1.2rem;
  line-height: 1.8;
  color: #ffffff;
}

.about-me p.details {
  margin-top: 1rem;
  font-size: 1.1rem;
  color: #ffeedd;
}
