.profile-image {
  width: 550px; /* Increased size for better visibility */
  height: 550px;
  border-radius: 50%;
  border: 3px solid #ddcdcd;
  margin: 1rem auto;
  display: block;
  object-fit: cover;
}

/* Media Query for Tablets */
@media (max-width: 768px) {
  .profile-image {
    width: 140px;
    height: 140px;
    object-position: 15% center; /* Shift visible area slightly for balance */
  }
}

/* Media Query for Phones */
@media (max-width: 480px) {
  .profile-image {
    width: 130px;
    height: 130px;
    object-position: 10% center;
  }
}
